import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function Triceps({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Группа мышц - Трицепс`} />
      <TopBanner style={tricepsStyle} title={'Упражнения для трицепса'} />
      {posts
        .filter(post => post.node.frontmatter.filterby === 'triceps')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const tricepsStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/5pmkzqzgv/triceps.jpg)',
	backgroundPosition: "center"
};

export const pageQuery = graphql`
  query TricepsQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



